<template>
    <div v-html="show_vip"></div>
</template>

<script>
import api_bonus from '@/common/es/api_bonus/index'

    export default {
        props: {
            item: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        computed: {
            show_vip() {
                if (this.item.vip) { 
                    let res = undefined
                    if (this.item.vip.star != 0) {
                        res = [ this.item.vip.name, this.item.vip.star ]
                    }
                    res = api_bonus.vip_view.code_to_content( res )
                    if (res) { return res.name } 
                }
                return '无'
            },
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


